import classnames from 'classnames';
import IconSelect from './icon-select';
import { __ } from '@wordpress/i18n';
import { RadioControl, BaseControl } from '@wordpress/components';

import { Component } from '@wordpress/element';

class SVGIconSelect extends Component {
	render() {
		const {
			iconControlTitle,
			iconPosition,
			onChangePosition,
			selectedIcon,
			onClickIcon,
			customInfo,
			customInfoStyle,
			previewIcon,
			align,
			isFloat,
		} = this.props;

		const iconBaseControlTitle =
			iconControlTitle === undefined
				? __('', 'ystandard-blocks')
				: iconControlTitle;

		return (
			<div
				className={classnames('ystdb-icon-select', {
					[`align--${align}`]: align,
					'is-float': isFloat,
				})}
			>
				{customInfo && <div style={customInfoStyle}>{customInfo}</div>}
				{!!onChangePosition && (
					<BaseControl>
						<div className="ystdb-inspector-controls__label">
							{__('', 'ystandard-blocks')}
						</div>
						<div className={'ystdb-icon-select__position'}>
							<RadioControl
								selected={iconPosition}
								options={[
									{
										label: __('', 'ystandard-blocks'),
										value: 'left',
									},
									{
										label: __('', 'ystandard-blocks'),
										value: 'right',
									},
								]}
								onChange={onChangePosition}
							/>
						</div>
					</BaseControl>
				)}
				<BaseControl>
					<div className="ystdb-inspector-controls__label">
						{iconBaseControlTitle}
					</div>
					<div className={'ystdb-icon-select__picker'}>
						<IconSelect
							selectedIcon={selectedIcon}
							previewIcon={previewIcon}
							onChange={(value) => {
								onClickIcon(value);
							}}
						/>
					</div>
				</BaseControl>
			</div>
		);
	}
}

export default SVGIconSelect;
